import { h } from '../../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../../node_modules/unfetch/dist/unfetch.mjs'
import Form, {
  FormGroup, Input, TextArea, SubmitForm, SubmitButton, Select,
} from '../../../../node_modules/@depack/form/build/index.js'
import { Col, ErrorAlert, Success } from './../../../frontend/components/Bootstrap.jsx'
import ArticleEditor from './../../ArticleEditor.jsx'
import FormImage from './../../Components/FormImage.jsx'
import { LoadingIndicator } from './../../Components/index.jsx'

export default class ObjectForm extends SubmitForm {
  constructor() {
    super()
    Object.assign(this.state, {
      loading: false,
      data: {},
      categories: [],
      hint: '1-комнатные-апартаменты-воскресенское',
      catSeo: 'апартаменты',
      article: '',
    })
  }
  async componentDidMount() {
    await this.loadCategories()

    const editing = !!this.props.id
    if (!editing) return
    this.setState({ editing: 1, loading: true })

    try {
      const res = await fetch(`/admin-data?objects&id=${this.props.id}`)
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else {
        const [d] = data
        this.setState({
          data: d,
          hint: d.seo,
          catSeo: d.categorySeo,
          article: d.article,
        })
      }
    } catch ({ message: error }) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  async loadCategories() {
    this.setState({ loading: true })
    try {
      const res = await fetch(`/admin-data?categories`)
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else {
        const categories = data.map(({ _id, title }) => ({
          value: _id,
          title,
        }))
        this.setState({ categories })
      }
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  getHint() {
    return `Фраза для поисковой оптимизации, участвующая в адресах страниц, например, knedv.ru/${this.state.catSeo}/<strong>${this.state.hint}</strong>.`
  }
  get editing() {
    return !!this.props.id
  }
  render({
    onClose, closeText = 'Отмена', successMessage, confirmText = 'Добавить', title, addedId,
  }) {
    const hint = this.getHint()
    const { categories, formLoading, data, loading, error, success } = this.state
    const form = (                     h(Form,{onSubmit:this.submit.bind(this)},
      h(FormGroup,{help:"Название для каталога недвижимости.",label:"Название"},
        h(Input,{value:data.title,name:"title",required:true,placeholder:"1к. апартаменты, 21 кв.м, п. Воскресенское"}),
      ),
      h(FormGroup,{help:"Цена объекта",label:"Цена"},
        h(Input,{value:data.price,name:"price",required:true,placeholder:"3 000 000 руб."}),
      ),
      h(FormGroup,{help:hint,label:"СЕО Название"},
        h(Input,{value:data.seo,name:"seo",required:true,placeholder:"1-комнатные-апартаменты-воскресенское"}),
      ),
      h(FormGroup,{help:"Описание объекта.",label:"Описание"},
        h(TextArea,{rows:10,name:"description",required:true,placeholder:"Новый торгово-гостиничный Комплекс «Воскресенский» в п. Воскресенское, который исполнен в стиле 'современная классика', что придает проекту свою индивидуальность и привлекательность в целях инвестиций. В комплексе будут развиты свои сервисные службы, и он станет достойным торгово-гостиничным комплексом, который будет являться частью п.Воскресенское: д/о Воскресенское, ФГАО Оздоровительный Комплекс «Архангельское» (Управ делами Президента РФ), детская балетная школа, хореографическая школа, детский центр творчества, детский музыкальный театр, студия музыкального развития, п. Юрьев Сад (таунхаусы), п. Кронбург (квадрохаусы), дачи известных людей СССР и политических деятелей нашего времени. Выгодные инвестиции (сдача в аренду посуточно, месячно, годично)."},
          data.description,
        ),
      ),

      h(FormImage,{editing:this.editing,help:"Картинка, отображаемая на главной странице.",required:true,image:data.cdnImage}),

      h(ArticleEditor,{article:this.state.article,name:"article",onSave:(html) => {
        this.setState({ article: html })
      }}),
      this.editing && h('input',{'value':this.props.id,'type':"hidden",'name':"id"}),
      h(FormGroup,{label:"Раздел",help:"Категория в каталоге"},
        h(Select,{options:categories,name:"category",value:data.category,required:true}),
      ),

      h(ErrorAlert,{error:error}),
      h(Success,{success:success,message:successMessage}),
      h(SubmitButton,{confirmText:confirmText,loading:formLoading,loadingText:"Загрузка..."}),

      addedId &&
        h('a',{'href':`/admin/albums/${addedId}`,'className':"ml-2 btn btn-warning"},
          `Загрузить Фотографии`
        ),

      onClose &&
        h('button',{'onClick':onClose,'type':"button",'className':"FormCancelBtn btn btn-secondary"},closeText)
      ,
    ))
    return      h(Col,{},
      title && h('h1',{},title),
      loading && h(LoadingIndicator),
      !loading && form,
    )
  }
}


