import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../node_modules/unfetch/dist/unfetch.mjs'
import Form, { FormGroup, Input, TextArea } from '../../../node_modules/@depack/form/build/index.js'
import { Col, ErrorAlert, Success } from './../../frontend/components/Bootstrap.jsx'
import ArticleEditor from './../ArticleEditor.jsx'
import FormImage from './../Components/FormImage.jsx'

export default class AddCategory extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: {},
      hint: 'москва-новостройки',
      article: '',
    }
  }
  async componentDidMount() {
    const editing = !!this.props.id
    if (!editing) return
    this.setState({ editing: 1, loading: true })
    try {
      const res = await fetch(`/admin-data?categories&id=${this.props.id}`)
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else {
        const [d] = data
        this.setState({ data: d, hint: d.seo, article: d.article })
      }
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  async submit(e) {
    this.setState({ error: null })
    e.preventDefault()
    const data = new FormData(e.currentTarget.data)
    this.setState({ formLoading: true })
    try {
      const res = await fetch('/admin-data?categories', {
        method: 'POST',
        body: data,
      })
      const { error } = await res.json()
      if (error) this.setState({ error })
      else this.setState({ success: 1 })
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ formLoading: false })
    }
    return false
  }
  getHint() {
    return `Фраза для поисковой оптимизации, участвующая в адресах страниц, например, knedv.ru/<strong>${this.state.hint}</strong>/3х-комнатные-квартиры.`
  }
  onFormChange(values) {
    const newSeo = values.seo
  }
  render() {
    const hint = this.getHint()
    const { editing } = this.state
    return                               h(Col,{},
      h('h1',{},this.state.editing ? 'Редактировать' : 'Добавить',` Категорию`),
      editing && this.state.loading && h('span',{'className':"echo-loader"},`Loading…`),
      !(editing && this.state.loading) &&
        h(Form,{onChange:(values) => {
          console.log(values)
          console.log(values.seo)
        },onSubmit:this.submit.bind(this)},
          h(FormGroup,{label:"Название",help:"Название для меню слева."},
            h(Input,{value:this.state.data.title,name:"title",placeholder:"Москва Новостройки",required:true}),
          ),
          h(FormGroup,{help:hint,label:"СЕО Название"},
            h(Input,{value:this.state.data.seo,required:true,name:"seo",placeholder:"москва-новостройки"}),
          ),
          h(FormGroup,{label:"Описание",help:"Краткое описание для главной страницы."},
            h(TextArea,{rows:"3",required:true,name:"description",placeholder:"Новая недвижиость в столице России -- это привлекательное предложения для тех, кто собирается строить свое будущее в центре событий."},
              this.state.data.description,
            ),
          ),
          h(FormImage,{editing:editing,help:"Картинка, отображаемая на главной странице.",required:true}),
          h(ArticleEditor,{article:this.state.article,onSave:(html) => {
            this.setState({ article: html })
          }}),
          editing && h('input',{'value':this.props.id,'type':"hidden",'name':"id"}),

          h('button',{'disabled':this.state.formLoading,'type':"submit",'className':"btn btn-primary"},this.state.formLoading ? 'Загрузка...' : `${editing ? 'Сохранить' : 'Добавить'}`),
          h(ErrorAlert,{error:this.state.error}),
          h(Success,{success:this.state.error,message:`Категория успешно ${editing ? 'сохранена' : 'создана'}!`}),
        )
      ,
    )
  }
}