import { h } from '../../../../node_modules/preact/dist/preact.mjs'
import Form, {
  FormGroup, Input, TextArea, SubmitForm, SubmitButton,
} from '../../../../node_modules/@depack/form/build/index.js'
import { Switch, Success, ErrorAlert } from './../../../frontend/components/Bootstrap.jsx'
import FormImage from './../../Components/FormImage.jsx'

export default class SpecialsForm extends SubmitForm {
  render({ item, onClose, closeText = 'Отмена', successMessage, confirmText = 'Добавить' }) {
    const i = item || {}
    const { formLoading, error, success } = this.state
    return (                                        h(Form,{onSubmit:this.submit.bind(this), onChange:() => {
      this.setState({ error: null, success: null })
    }},
      h(FormGroup,{label:"Название",help:"Заголовок для главной страницы, напр., Ленинский проспект, дом 114"},
        h(Input,{value:i.title,placeholder:"Название акции",name:"title",required:true}),
      ),
      h(FormGroup,{label:"Описание",help:"Введите описание акции..."},
        h(TextArea,{name:"description",required:true,placeholder:"Описание акции"},i.description),
      ),

      h(FormImage,{editing:item,help:"Картинка, отображаемая на главной странице.",required:true,image:i.cdnImage}),

      h(FormGroup,{label:"Цена",help:"Задайте цену..."},
        h(Input,{value:i.price,name:"price",placeholder:"55 000 000 руб."}),
      ),

      h(FormGroup,{label:"Переход",help:"Ссылка на страницу каталога, или сайта."},
        h(Input,{value:i.href,name:"href",placeholder:"/каталог/москва-элитная/лениниский-проспект-дом-114"}),
      ),

      h(FormGroup,{help:"Добавить в специальные предложения на главной."},
        h(Switch,{value:i.show_on_main,label:"Отображать на главной",name:"show_on_main"}),
      ),

      item && h('input',{'value':i._id,'type':"hidden",'name':"id"}),

      h(ErrorAlert,{error:error}),
      h(Success,{success:success,message:successMessage}),

      h(SubmitButton,{confirmText:confirmText,loading:formLoading,loadingText:"Загрузка..."}),

      onClose &&
        h('button',{'onClick':onClose,'type':"button",'className':"FormCancelBtn btn btn-secondary"},closeText)
      ,
    ))
  }
}